/*--------------------------------------------------------------
 *# General
 *-------------------------------------------------------------- */

body {
  font-family: "Open Sans", sans-serif;
  color: #ffffff;
  background-color: #303030; }

a {
  color: #bb0f0f;
  text-decoration: none;

  &:hover {
    color: #bb0f0f;
    text-decoration: none; } }

h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif; }

/*--------------------------------------------------------------
 *# Back to top button
 *-------------------------------------------------------------- */

.back-to-top {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  right: 1px;
  bottom: 15px;
  z-index: 996;
  background: #000000a9;
  width: 45px;
  height: 45px;
  border-radius: 4px;
  transition: all 0.4s;
  i {
    font-size: 28px;
    color: #fff;
    line-height: 0;
    transition: 0.4s; }
  @media (min-width: 767.98px) {
    &:hover {
      width: 55px;
      i {
        color: #bb0f0f;
        transition: .4s; } } }

  &.active {
    visibility: visible;
    opacity: 1; } }

.text-call {
  position: fixed;
  right: 1px;
  bottom: 10px;
  z-index: 996;
  background: #000000a9;
  width: 45px;
  height: 45px;
  border-radius: 4px;
  transition: all 0.4s;
  i {
    color: #fff;
    font-size: 20px;
    line-height: 0;
    transition: 0.4s; }
  @media (min-width: 767.98px) {
    &:hover {
      width: 55px;
      i {
        color: #bb0f0f;
        transition: .4s; } } } }

.back-call {
  margin-bottom: 225px; }

.youtube {
  margin-bottom: 170px; }

.instagramm {
  margin-bottom: 115px; }

.facebook {
  margin-bottom: 60px; }


/*--------------------------------------------------------------
 *# Disable aos animation delay on mobile devices
 *-------------------------------------------------------------- */
@media screen and (max-width: 768px) {
  [data-aos-delay] {
    transition-delay: 0 !important; } }

/*--------------------------------------------------------------
 *# Header
 *-------------------------------------------------------------- */

#header {
  background: #303030;
  transition: all 0.5s;
  z-index: 997;
  padding: 0px 0;
  box-shadow: 0px 5px 6px #000;
  &.header-scrolled {
    padding: 0;
    box-shadow: 0px 5px 6px #000; }

  .logo {
    font-size: 30px;
    margin: 0;
    padding: 0;
    font-weight: 600;
    font-family: "Poppins", sans-serif;

    a {
      color: #111111;

      span {
        color: #e03a3c; } }

    img {
      max-height: 60px;
      padding: 0; } } }

/*--------------------------------------------------------------
 *# Navigation Menu
 *-------------------------------------------------------------- */
/**
 ** Desktop Navigation */

.navbar {
  padding: 0;
  background-color: #303030;

  ul {
    margin: 0;
    padding: 0;
    display: flex;
    list-style: none;
    align-items: center; }

  li {
    position: relative; }

  a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0 10px 30px;
    font-family: "Raleway", sans-serif;
    font-size: 15px;
    font-weight: 600;
    color: #ffffff;
    //text-shadow: 1px 1px 1px #000
    white-space: nowrap;
    transition: 0.4s;

    i {
      font-size: 12px;
      line-height: 0;
      margin-left: 5px; }

    &:hover {
      color: #bb0f0f; } }

  .active, li:hover > a {
    color: #bb0f0f; }

  .dropdown {
    ul {
      display: block;
      position: absolute;
      left: -100px;
      top: calc(100% + 30px);
      margin: 0;
      padding: 10px 0;
      z-index: 99;
      opacity: 0;
      visibility: hidden;
      border: 2px solid #2b2a2a;
      border-radius: 3px;
      background: #bb0f0f;
      box-shadow: 10px 10px 7px #000000;
      transition: 0.4s;

      li {
        min-width: 200px; }

      a {
        padding: 10px 20px;
        font-size: 16px;

        i {
          font-size: 12px; }

        &:hover {
          color: #000; } }

      .active:hover, li:hover > a {
        color: #000; } }

    &:hover > ul {
      opacity: 1;
      top: 100%;
      visibility: visible; }

    .dropstart {
      ul {
        top: 0;
        left: calc(100% - 30px);
        visibility: hidden; }

      &:hover > ul {
        opacity: 1;
        top: 0;
        left: 100%;
        visibility: visible; } } } }

@media (max-width: 1366px) {
  .navbar .dropdown .dropdown {
    ul {
      left: -90%; }

    &:hover > ul {
      left: -100%; } } }

/**
 ** Mobile Navigation */

.mobile-nav-toggle {
  color: #bb0f0f;
  font-size: 50px;
  cursor: pointer;
  display: none;
  line-height: 0;
  transition: 0.5s;

  &.bi-x {
    color: #bb0f0f; } }

@media (max-width: 991px) {
  .mobile-nav-toggle {
    display: block; }

  .navbar ul {
    display: none; } }

.navbar-mobile {
  position: fixed;
  overflow: hidden;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  transition: 0.3s;
  z-index: 999;

  .mobile-nav-toggle {
    position: absolute;
    font-size: 60px;
    top: 15px;
    right: 15px; }

  ul {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 20%;
    padding: 150px 0;
    background-color: #181818;
    overflow-y: auto;
    transition: 0.5s;
    z-index: 9; }

  a {
    padding: 10px 20px;
    font-size: 18px;
    color: #ffffff;

    &:hover {
      color: #bb0f0f; } }

  .active, li:hover > a {
    color: #bb0f0f; }

  .dropdown {
    ul {
      position: static;
      display: none;
      margin: 10px 20px;
      padding: 10px 0;
      z-index: 99;
      opacity: 1;
      visibility: visible;
      background: #252525;
      box-shadow: 0px 0px 30px rgba(127, 137, 161, 0.25);

      li {
        max-width: 100px; }

      a {
        padding: 10px 20px;

        i {
          font-size: 18px; }

        &:hover {
          color: #bb0f0f; } }

      .active:hover, li:hover > a {
        color: #bb0f0f; } }

    > .dropdown-active {
      display: block; } } }


