/*--------------------------------------------------------------
 *# Sections General
 *-------------------------------------------------------------- */

section {
  padding: 60px 0;
  overflow: hidden;
  position: relative; }

.section-title {
  text-align: center;
  padding-bottom: 30px;
  position: relative;

  h2 {
    font-size: 32px;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 20px;
    padding-bottom: 20px;
    position: relative;
    color: #bb0f0f;
    text-shadow: 3px 3px 8px #000000;

    &::after {
      content: '';
      position: absolute;
      display: block;
      width: 50px;
      height: 3px;
      background: #bb0f0f;
      box-shadow: 3px 3px 8px #000000;
      bottom: 0;
      left: calc(50% - 25px); } }

  p {
    margin-bottom: 0; } }

.section-bg {
  padding: 120px 0;
  color: #fff;

  &:before {
    content: "";
    background: #1b1b1b;
    position: absolute;
    bottom: 60px;
    top: 60px;
    left: 0;
    right: 0;
    transform: skewY(-3deg); } }

/*--------------------------------------------------------------
 *# Breadcrumbs
 *-------------------------------------------------------------- */

.breadcrumbs {
  padding: 15px 0;
  background: #2b2b2b;
  min-height: 40px;
  margin-top: 82px;
  color: #fff;

  h2 {
    font-size: 28px;
    font-weight: 500;
    color: #bb0f0f; }

  ol {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0 0 10px 0;
    margin: 0;
    font-size: 14px;

    a {
      color: #aaaaaa;

      &:hover {
        color: #fff;
        transition: 0.3s; } }

    li + li {
      padding-left: 10px;

      &::before {
        display: inline-block;
        padding-right: 10px;
        color: #bb0f0f;
        content: "/"; } } } }

